import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import BottomNavigation from "@material-ui/core/BottomNavigation";
import BottomNavigationAction from "@material-ui/core/BottomNavigationAction";
const useStyles = makeStyles({
  root: {
    position: "absolute",
    bottom: "0",
    width: "100%",
    borderTop: "1px solid lightgray",
  },
  bottom_nav: {
    transition: "border 1s linear",
  },
  icons: {
    width: "1.75rem",
  },
  nav_label: {
    fontSize: "0.8rem",
    fontWeight: "bold",
    transition: "margin 1s ease",
  },
});

const BottomNavbar = () => {
  const classes = useStyles();
  const [value, setValue] = React.useState("menu");

  const handleChange = (event, newValue) => {
    setValue(newValue);
    // props.setPage(value)
  };

  return (
    <BottomNavigation
      value={value}
      onChange={handleChange}
      className={classes.root}
      showLabels
    >
      <BottomNavigationAction
        className={classes.bottom_nav}
        style={{
          filter: value === "menu" ? "grayscale(0)" : "grayscale(1)",
          borderTop: value === "menu" ? "1.2px solid black" : "none",
        }}
        label={
          <span
            style={{
              marginTop: value === "menu" ? "1.5px" : "5px",
              color: value === "menu" ? "gray" : "black",
            }}
            className={classes.nav_label}
          >
            Menu
          </span>
        }
        value="menu"
        icon={
          <img
            className={classes.icons}
            alt="menu-icon"
            src="https://img.icons8.com/dusk/64/000000/restaurant-menu.png"
          />
        }
      />
      <BottomNavigationAction
        className={classes.bottom_nav}
        style={{
          filter: value === "cart" ? "grayscale(0)" : "grayscale(1)",
          borderTop: value === "cart" ? "1.2px solid black" : "none",
        }}
        label={
          <span
            style={{
              marginTop: value === "cart" ? "1.5px" : "5px",
              color: value === "cart" ? "gray" : "black",
            }}
            className={classes.nav_label}
          >
            Cart
          </span>
        }
        value="cart"
        icon={
          <img
            className={classes.icons}
            alt="cart-icon"
            src="https://img.icons8.com/fluent/48/000000/shopping-basket-2.png"
          />
        }
      />
      <BottomNavigationAction
        className={classes.bottom_nav}
        style={{
          filter: value === "bill" ? "grayscale(0)" : "grayscale(1)",
          borderTop: value === "bill" ? "1.2px solid black" : "none",
        }}
        label={
          <span
            style={{
              marginTop: value === "bill" ? "1.5px" : "5px",
              color: value === "bill" ? "gray" : "black",
            }}
            className={classes.nav_label}
          >
            Bill
          </span>
        }
        value="bill"
        icon={
          <img
            className={classes.icons}
            alt="bill-icon"
            src="https://img.icons8.com/fluent/48/000000/bill.png"
          />
        }
      />
    </BottomNavigation>
  );
};

export default BottomNavbar;
